.login-form {
  max-width: 300px; 
}
.login-form-forgot {
  float: right;
}
.login-form-button {
  width: 100%;
}

 .logo {
  width: 120px;
  height: 31px;
  background: rgba(255,255,255,.2);
  margin: 16px 24px 16px 0;
  float: left;
}

.theNav {
  background-color: #FF9801;
}

.theFooter {
  position: fixed;
  bottom: 0;
  width: 100%;
}

/* The only rule that matters */
.video-background {
  /*  making the video fullscreen  */
    position: fixed;
    right: 0; 
    bottom: 0;
    min-width: 100%; 
    min-height: 100%;
    width: auto; 
    height: auto;
    z-index: -100;
  }

